export default {
	data() {
		return {
			height: '',
			opens: -1,
			merchantId:'',
			orders:'',
		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
				that.refundOrder();
			}
		});
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.load_newOrders();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onReady() {
		this.getTopheight();
	},
	methods: {
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
				}
			})
		},
		wz: function(latitude, longitude) {
			uni.openLocation({
				latitude: latitude,
				longitude: longitude,
				success: function() {
				}
			});
		},
		dh: function(phone) {
			uni.makePhoneCall({
				phoneNumber: phone,
				success: function(res) {
				}
			});
		},
		open: function(index) {
			this.tg = !this.tg;
			if (this.opens != index) {
				this.opens = index;
			} else {
				this.opens = -1;
			}
		},
		refundOrder: function() {
			let that = this;
			uni.request({
				url: this.serveipd + '/api/merchant/order/refundOrder',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
				},
				success: res => {
					that.orders = res.data.data;
					console.log(res)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		see_infor: function(orderno) {
			let that = this;
			uni.navigateTo({
				url:"./refund_details?orderno="+orderno,
			})
		},
	}
}
